@font-face {
  font-family: 'biotif';
  src:
    url('./assets/fonts/dm-sans.woff2') format('woff2 supports variations'),
    url('./assets/fonts/dm-sans.woff2') format('woff2-variations');
  font-weight: 100 1000;
  font-style: normal;
}
html {
  font-family: 'biotif';
  font-size: 100%;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* src/ArabicKeyboard.css */
.App {
  width: 100vw;
  height: 100vh;
  padding: 20px;
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  background-color: #fff;
}
.keyboardInput {
  display: inline-block;
  width: 100%;
  margin-top: 40px;
  font-size: 24px;
  border: 2px solid #E0C097;
  border-radius: 5px;
  outline: none;
  padding: 4px;
}
.arwiHeading {
  font-family: "Fustat", sans-serif;
  font-optical-sizing: auto;
  font-size: 40px;
}
.arabic-keyboard {
  width: 100%;
  padding: 8px;
  border: 1px solid #E0C097;
  border-radius: 10px;
  background-color: #ffffff;
}

.keyboard-row {
  display: flex;
  margin-bottom: 5px;
}

.key {
  flex: 1;
  margin: 2px;
  padding: 6px;
  font-size: 10px;
  font-weight: 500;
  cursor: pointer;
  border: 2px solid #E0C097;
  border-radius: 8px !important;
  color: #000;
  background-color: #fff;
  transition: background-color 0.2s;
}

.key:hover {
  background-color: #f6f0eb;
  color: #000;
}

.actionContainer{
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 24px;
   flex-wrap: wrap;
   margin: 10px 0;
}
.actionContainer button {
  height: 28px;
  width: 60px;
  font-size: 10px;
  border: none;
  border-radius: 8px;
  color: #fff;
  font-weight: 600px;
  background-color: #287796;
  cursor: pointer;
}
.actionContainer button:hover {
  background-color: #fff;
  border: 2px solid #E0C097;
  color: #000;
}
@media (min-width: 768px) {
  .App {
    padding: 60px;
  }
  .key {
    font-size: 16px;
    border-radius: 2px;
    padding: 10px;
  }
  .keyboardInput {
    padding: 10px;
  }
  .actionContainer{
    margin: 20px 0;
  }
  .actionContainer button {
    height: 40px;
    width: 100px;
    font-size: 14px;
  }
  .arabic-keyboard {
    padding: 24px;
  }
}
.alert{
  position: absolute;
  top: 24px;
  left: 50%;
  transform: translateX(-50%);
  width: 30%;
  height: 100px;
  background-color: #fff;
  border: 4px solid #00804b;
  font-weight: 600;
  color: #131842;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: alert-animation .2s ease-in-out forwards;
}

@keyframes alert-animation {
  from {
    transform: translate(-50%, -20px);
  }
  to {
    transform: translate(-50%, 0);
  }
}